<template>
  <div class="bloglist">
    <div v-for="(item,i) in list[nowpage-1]" v-bind:key="i">
      <div class="blogitem" v-on:click="chooseblog($event)">{{item}}</div>
    </div>
    <div class="page">
      <button class="pageup" v-on:click="pageup()">上一页</button>
      <div class="nowpage">当前 第{{nowpage}}页/共{{page}}页</div>
      <button class="pagedown" v-on:click="pagedown()">下一页</button>
    </div>
  </div>
</template>
<script>
import store from "../../store/index";
function getTime(nS) {
  var date = new Date(parseInt(nS));
  var year = date.getFullYear();
  var mon = date.getMonth() + 1;
  var day = date.getDate();
  var hours = date.getHours();
  var minu = date.getMinutes();
  var sec = date.getSeconds();
  var mil = date.getMilliseconds();
  var time = {
    year,
    mon,
    day,
    hours,
    minu,
    sec,
    mil
  };
  return time;
}
function transTwoArry(str, num) {
  // 数组分组
  var data,
    result = [];
  if (str instanceof Array) {
    data = str;
  } else if (typeof str == "string") {
    data = str.split(",");
  } else {
    data = Object.entries(str);
  }
  if (typeof num == "string") {
    num = Number(num);
  }
  for (var i = 0, len = data.length; i < len; i += num) {
    result.push(data.slice(i, i + num));
  }
  return result;
}

export default {
  name: "bloglist",
  data() {
    return {
      list: [],
      page: 0,
      nowpage: 1
    };
  },
  methods: {
    pageup() {
      if (this.nowpage != 1) {
        this.nowpage = this.nowpage - 1;
      }
    },
    pagedown() {
      if (this.nowpage != this.page) {
        this.nowpage = this.nowpage + 1;
      }
    },
    chooseblog(e) {
      let tit = e.target.innerText.split("_");
      store.commit("changetit", tit[1]);
      store.commit("changedate", getTime(tit[0]));     
      this.$http
        .get(`http://${window.location.hostname}:9090/api/chooseblog`, {
          params: { choose: `${e.target.innerText}.md` }
        })
        .then(response => {
          store.commit("changedata", response.data);
          console.groupCollapsed("选择的blog内容");
          console.log(global);
          console.groupEnd();
        })
        .catch(function(error) {
          // 请求失败处理
          console.log(error);
        });
      store.commit("istrue", true);
    }
  },
  mounted() {
    this.$http
      .get(`http://${window.location.hostname}:9090/api/bloglist`)
      .then(response => {
        this.list = transTwoArry(response.data, 6);
        this.page = this.list.length;
      })
      .catch(function(error) {
        // 请求失败处理
        console.log(error);
      });
  },
  beforeMount() {
    //   挂载之前创建
  }
};
</script>
<style lang="css">
.blogitem {
  margin: 30px;
  padding: 10px;
  font-size: 22px;
  color: #333;
  background: rgb(86, 255, 100);
  border-radius: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  box-shadow: 0 0 1px rgb(0, 131, 55);
}
.blogitem:hover {
  box-shadow: 0 0 3px rgb(0, 131, 55);
}
.blogitem:active {
  box-shadow: 0 0 3px rgb(0, 131, 55) inset;
}
.page {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  font-family: "幼圆";
  text-align: center;
}
.pageup,
.pagedown {
  width: 70px;
  padding: 5px 0;
  border-radius: 5px;
  justify-items: center;
  background: rgb(0, 174, 255);
  box-shadow: 0 0 2px rgb(0, 3, 75);
  outline: none;
  border: none;
  transition: all 0.2s;
}
.pageup:hover,
.pagedown:hover {
  background: rgb(0, 52, 194);
  color: #fff;
}
.pageup:active,
.pagedown:active {
  box-shadow: 0 0 3px rgb(0, 3, 75) inset;
}
.nowpage {
  width: 160px;
  padding: 5px 0;
  border-radius: 5px;
  justify-items: center;
  background: rgb(0, 68, 255);
  box-shadow: 0 0 5px rgb(0, 3, 75);
  color: #fff;
}
</style>