<template>
  <div class="blogedit" v-if="this.$store.state.istrue">
    <div class="tit">
      <div class="point">博客标题</div>
      <input type="text" class="text" :value="this.$store.state.edittit" />
    </div>
    <div class="date">
      <div class="point">博客创建日期</div>
      <input type="text" class="timetext" id="year" :value="this.$store.state.editdate.year" /> 年
      <input type="text" class="timetext" :value="this.$store.state.editdate.mon" /> 月
      <input type="text" class="timetext" :value="this.$store.state.editdate.day" /> 日
      <input type="text" class="timetext" :value="this.$store.state.editdate.hours" /> 时
      <input type="text" class="timetext" :value="this.$store.state.editdate.minu" /> 分
      <input type="text" class="timetext" :value="this.$store.state.editdate.sec" /> 秒
      <input
        type="text"
        class="timetext"
        :value="this.$store.state.editdate.mil"
        style="display:none;"
      />
    </div>
    <div class="editcontent">
      <div class="point">博客内容</div>
      <textarea name="textarea" id="textarea" :value="this.$store.state.editcontent"></textarea>
    </div>
    <div class="button">
      <button class="submit" id="upsubmit" v-on:click="upsubmit()">修改</button>
      <button class="submit" id="desubmit" v-on:click="desubmit()">删除</button>
    </div>

    <!-- 记住了store如何使用 -->
  </div>
</template>
<script>
import store from "../../store/index";
export default {
  name: "blogedit",
  methods: {
    upsubmit() {
      let date = [];
      document.getElementsByClassName("timetext").forEach(element => {
        date.push(element.value);
      });
      $.ajax({
        url: `http://${window.location.hostname}:9090/api/editblog`,
        type: "post",
        data: {
          tit: document.getElementsByTagName("input")[0].value,
          date: date,
          data: document.getElementsByTagName("textarea")[0].value
        },
        dataType: "text",
        success: function(res) {
          location.reload();
          // console.log(res);
        },
        error: function(err) {
          alert("上传失败", err);
        }
      });
    },
    desubmit() {
      this.$http
        .get(`http://${window.location.hostname}:9090/api/unblog`)
        .then(response => {
          console.log(response);
        })
        .catch(function(error) {
          // 请求失败处理
          console.log(error);
        });
      store.commit("isupdata");
      this.$nextTick(() => {
        store.commit("isupdata");
      });
      store.commit("istrue", false);
    }
  }
};
</script>
<style lang="css">
.point {
  color: #fff;
  padding: 3px 0;
  font-family: "幼圆";
  height: 20px;
  text-align: center;
  background: rgb(0, 68, 255);
  border-radius: 10px;
  margin: 10px;
  box-shadow: 0 0 2px rgb(0, 3, 75);
}
.editcontent {
  height: 100%;
}
textarea {
  height: 280px;
  margin: 10px 0 0 0;
  width: 96%;
  outline: none;
  border: none;
  padding: 2px 2%;
  background: rgb(247, 247, 247);
  color: #333;
  box-shadow: 0 0 1px rgb(0, 131, 55);
  border-radius: 10px;
}
textarea:focus {
  box-shadow: 0 0 3px rgb(0, 77, 32);
}
.text {
  font-size: 18px;
  margin-left: 10px;
  padding: 3px;
  outline: none;
  border: none;
  background: rgb(86, 255, 100);
  color: #333;
  box-shadow: 0 0 1px rgb(0, 131, 55);
  border-radius: 10px;
}
.text:focus {
  box-shadow: 0 0 3px rgb(0, 73, 6);
}
.timetext {
  font-size: 18px;
  padding: 3px;
  outline: none;
  border: none;
  background: rgb(86, 255, 100);
  color: #333;
  box-shadow: 0 0 1px rgb(0, 131, 55);
  border-radius: 10px;
  width: 30px;
}
.timetext:focus {
  box-shadow: 0 0 3px rgb(0, 73, 6);
}
#year {
  width: 50px;
  margin-left: 10px;
}
.button {
  display: flex;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  height: 40px;
}
.submit {
  width: 80px;
  font-size: 18px;
  outline: none;
  border: none;
  background: rgb(86, 255, 100);
  color: #333;
  border-radius: 5px;
  box-shadow: 0 0 4px rgb(0, 131, 55);
}
#desubmit {
  background: rgb(255, 83, 83);
}
#desubmit:hover {
  background: rgb(255, 42, 42);
}
#upsubmit:hover {
  background: rgb(49, 255, 42);
}
</style>